.circular{animation:rotate 2s linear infinite;height:48px;position:relative;width:48px}.path{stroke-dasharray:0.95, 190;stroke-dashoffset:0;animation:dash 1.5s ease-in-out infinite}.theme-owner .path{stroke:#2362C7}.theme-helper .path{stroke:#4ca9d8}.theme-admin .path{stroke:#03dac6}.progress-fullscreen{position:fixed;top:0;left:0;z-index:1051;display:flex;justify-content:center;align-items:center;width:100%;height:100%;background:rgba(255,255,255,0.7)}@keyframes rotate{100%{transform:rotate(360deg)}}@keyframes dash{0%{stroke-dasharray:0.95, 190;stroke-dashoffset:0}50%{stroke-dasharray:84.55, 190;stroke-dashoffset:-33.25}100%{stroke-dasharray:84.55, 190;stroke-dashoffset:-117.8}}
